import React, { useState } from 'react';
import classes from './Pagination.module.css';



let Pagination = ({ totalItemsCount, pageSize, currentPage, onPageChanged, portionSize = 10}) => {

    let pagesCount = Math.ceil(totalItemsCount / pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    let portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionPageNumber = portionNumber * portionSize;

    return <div className={classes.pages}>
        {portionNumber > 1 && <button className={classes.button} onClick={() => {setPortionNumber(portionNumber - 1)}}>&larr;</button> }

        {pages.filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
            .map(p => {
            return <span onClick={() => {onPageChanged(p) }}
                className={currentPage === p && classes.selectedPage || classes.pages}>{p}
            </span>
            })}
        {portionCount > portionNumber && <button className={classes.button} onClick={() => setPortionNumber(portionNumber + 1)}>&rarr;</button> }
    </div>
}

export default Pagination;